.button-arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 0;
  transition: background-color var(--transition-duration)
    var(--transition-timing-function);
}

.button-arrow:hover,
.button-arrow:focus {
  transform: scale(1.1);
}

.list-item {
  @media screen and (min-width: 768px) {
    display: flex;
    margin-left: -16px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: -32px;
  }
}
