*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

p, h1, h2, h3, h4, h5, h6, ul, ol {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

img {
  height: auto;
  display: block;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-primary);
  letter-spacing: .04em;
  font-size: 14px;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

:root {
  --font-primary: "DM Sans", sans-serif;
  --font-secondary: "Titan One", cursive;
  --cl-light: #fff;
  --cl-dark: #000;
  --cl-brown: #907e82;
  --cl-red: #d41443;
  --cl-pink: #ffa5ba;
  --bg-light: #fff;
  --bg-pink: #ffb8ca;
  --bg-lightpink: #fff5f6;
  --bg-beige: #f0d1a5;
  --bg-green: #c2e297;
  --bg-red: #d41443;
  --bg-hover: #ecebeb;
  --bg-accent-hover: #c7133e;
  --bg-modal: #e17992;
  --bg-grey: #fafafa;
  --bg-border: #e1e1e1;
  --bg-btnloc: #d41342;
  --transition-duration: 250ms;
  --transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.button {
  height: 44px;
  background-color: var(--bg-light);
  color: var(--cl-red);
  border: rgba(0, 0, 0, 0);
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  display: flex;
}

.button:hover, .button:active, .button:focus {
  background-color: var(--bg-hover);
}

.button--accent {
  background-color: var(--bg-red);
  color: var(--cl-light);
}

.button--accent:hover, .button--accent:active, .button--accent:focus {
  background-color: var(--bg-accent-hover);
}

.header {
  z-index: 3;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__container {
    padding-top: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .header__container {
    justify-content: space-between;
    padding-top: 37px;
  }
}

.logo {
  color: var(--cl-red);
  font-size: 26px;
  line-height: 1.15;
  font-family: var(--font-secondary);
  text-transform: uppercase;
  align-items: center;
  display: flex;
}

.logo__img {
  margin-right: 20px;
}

.nav {
  display: none;
}

@media screen and (min-width: 1280px) {
  .nav {
    display: block;
  }
}

.nav__list {
  gap: 35px;
  display: flex;
}

.nav__link {
  color: var(--cl-light);
  transition-property: color;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
  padding-top: 18px;
  padding-bottom: 18px;
  font-weight: 700;
}

.nav__link:hover, .nav__link:focus, .nav__link:active {
  color: var(--cl-red);
}

.header__button {
  transition-property: opacity, background-color;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
  padding-left: 37px;
  padding-right: 37px;
  font-size: 16px;
  display: none;
}

@media screen and (min-width: 768px) {
  .header__button {
    gap: 12px;
    animation-name: pulse;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-delay: 2.5s;
    display: flex;
  }
}

@keyframes pulse {
  50% {
    box-shadow: 0 5px 0 -5px #fff, 0 -5px 0 -5px #fff, 5px 0 0 -5px #fff, -5px 0 0 -5px #fff, 0 0 0 5px #fff5f6, 5px 5px 5px 5px rgba(255, 36, 235, 0);
  }
}

.mobile-button {
  width: 20px;
  height: 16px;
  transition-property: opacity;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
  opacity: 1;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("burger.358204b6.svg");
  background-position: center;
  background-repeat: no-repeat;
  border-color: rgba(0, 0, 0, 0);
}

.mobile-button.is-open {
  pointer-events: none;
  opacity: 0;
}

@media screen and (min-width: 768px) {
  .mobile-button {
    margin-left: auto;
    margin-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .mobile-button {
    display: none;
  }
}

@media screen and (max-width: 1279px) {
  .mobile-button.is-open ~ .header__button {
    opacity: 0;
    pointer-events: none;
  }
}

.mobile-button--cross {
  background-image: url("cross.11935692.svg");
  margin: 0;
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .mobile-button--cross {
    right: 32px;
  }
}

@keyframes show {
  from {
    opacity: .6;
  }

  to {
    opacity: 1;
  }

  90% {
    opacity: .99;
  }
}

